import { computed, effect, signal, Signal } from "@preact/signals";
import {
	AdminExternalPartner,
	AdminUser,
	ColorSchemeType,
	CoordinatorMission,
	Filter,
	MissionHolderService,
	PartnerFullMission,
	UserInfo,
	ViewType,
} from "../Common/CommonModel";
import { SignalToValue } from "../utils/signalUtils";

/** The type of the global state of the application. */
export type GlobalState = {
	/** the color scheme of the application */
	colorScheme: Signal<ColorSchemeType>;
	/** if the screen is wake locked */
	isWakeLock: Signal<boolean>;
	/** if the screen is above md */
	isAboveMd: Signal<boolean>;
	/** if the screen is below xxs */
	isBelowXxs: Signal<boolean>;
	/** the size of the viewport */
	viewportSize: Signal<{
		/** the height of the viewport */
		height: number;
		/** the width of the viewport */
		width: number;
	}>;
	/** if the document is visible */
	isDocumentVisible: Signal<boolean>;
	/** the logged in user information */
	userInfo: Signal<UserInfo | null>;
	/** the list of filters */
	filterList: Signal<Filter[]>;
	/** the current view of the application */
	view: Signal<ViewType>;
	/** the list of missions for coordinator */
	coordinatorMissionList: Signal<Signal<CoordinatorMission>[]>;
	/** the list of full missions for partner */
	partnerFullMissionList: Signal<Signal<PartnerFullMission>[]>;
	/** the list of admin users */
	adminUserList: Signal<Signal<AdminUser>[]>;
	/** the list of admin external partners */
	adminPartnerList: Signal<AdminExternalPartner[]>;
	/** the list of mission holder services */
	missionHolderServiceList: Signal<MissionHolderService[]>;
};

// Note: all of theses settings could be stored in the database
type LocalStorageState = SignalToValue<Pick<GlobalState, "colorScheme" | "filterList" | "view" | "userInfo">>;

const loadGlobalState = (): GlobalState => {
	const storedGlobalState = JSON.parse(localStorage.getItem("globalState") ?? "{}") as Partial<SignalToValue<LocalStorageState>>;

	return {
		colorScheme: signal(storedGlobalState.colorScheme ?? "dark"),
		isWakeLock: signal(false),
		isAboveMd: signal(false),
		isBelowXxs: signal(false),
		isDocumentVisible: signal(true),
		viewportSize: signal({ height: 0, width: 0 }),
		filterList: signal(storedGlobalState.filterList ?? []),
		view: signal(storedGlobalState.view ?? "card"),
		userInfo: signal(storedGlobalState.userInfo ?? null),
		coordinatorMissionList: signal([]), // will be set after usedLimoList
		partnerFullMissionList: signal([]), // will be set after usedLimoList
		adminUserList: signal([]), // will be set on connection
		adminPartnerList: signal([]), // will be set on connection
		missionHolderServiceList: signal([]), // will be set on connection
	};
};

/** The global state of the application. */
export const globalState: GlobalState = loadGlobalState();

const localStorageState = computed(
	(): LocalStorageState => ({
		colorScheme: globalState.colorScheme.value,
		filterList: globalState.filterList.value,
		view: globalState.view.value,
		userInfo: globalState.userInfo.value,
	})
);

/** "md" if the screen is above md, "sm" otherwise. */
export const smMd = computed(() => (globalState.isAboveMd.value ? "md" : "sm"));
/** "sm" if the screen is above md, "xs" otherwise. */
export const xsSm = computed(() => (globalState.isAboveMd.value ? "sm" : "xs"));
/** "compact-md" if the screen is above md, "compact-sm" otherwise. */
export const compactXsSm = computed(() => `compact-${xsSm.value}`);

effect(() => localStorage.setItem("globalState", JSON.stringify(localStorageState.value)));

effect(() => void document.body.classList.toggle("dark", globalState.colorScheme.value === "dark"));
